import { cn } from "@/lib/utils";

interface MowglaiLogoProps {
  className?: string;
  size?: "sm" | "md" | "lg" | "xl";
}

const MowglaiLogo = ({ className, size = "md" }: MowglaiLogoProps) => {
  const sizeClasses = {
    sm: "w-10 h-10",
    md: "w-14 h-14",
    lg: "w-20 h-20",
    xl: "w-32 h-32",
  };

  const getImageUrl = (filename: string) => {
    return `/${filename}`;
  };

  return (
    <div
      className={cn(
        "relative flex items-center justify-center overflow-hidden rounded-full",
        "bg-background/5 border border-primary/20 backdrop-blur-sm",
        "transition-transform duration-500 hover:scale-105",
        sizeClasses[size],
        className
      )}
    >
      {/* Dark Mode Logo */}
      <img
        src={getImageUrl("logo1.png")}
        alt="Mowglai"
        className="w-full h-full object-cover hidden dark:block"
      />
      {/* Light Mode Logo */}
      <img
        src={getImageUrl("logo2.png")}
        alt="Mowglai"
        className="w-full h-full object-cover block dark:hidden"
      />
    </div>
  );
};

export default MowglaiLogo;
